
.nav-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.88rem;
  background-color: #2EB5E5;
  display: -ms-flexbox;
  display: flex;
}
.nav-container > div {
    -ms-flex: 1;
        flex: 1;
}
.nav-container .header-left {
    padding: 0.04rem 0;
}
.nav-container .header-left img {
      display: block;
      width: 0.6rem;
      height: 0.6rem;
      border-radius: 0.3rem;
      vertical-align: middle;
}
.chatroom {
  position: absolute;
  left: 0;
  top: 0.88rem;
  bottom: 0.78rem;
  width: 100%;
  background: #f5f5f5;
  overflow-y: scroll;
}
.chatroom .inBox .message {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding: 0.2rem;
}
.chatroom .inBox .message .info {
      width: 0.56rem;
      height: 100%;
      margin: 0 0.2rem;
      min-height: 0.56rem;
      position: relative;
}
.chatroom .inBox .message .info img {
        display: block;
        border: none;
        width: 0.56rem;
        height: 0.56rem;
        border: 1px solid #e5e5e5;
        border-radius: 0.28rem;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
}
.chatroom .inBox .message p.item {
      font-size: 0.28rem;
      line-height: 0.36rem;
      min-height: 0.36rem;
      max-width: 70%;
      text-align: left;
      padding: 0.1rem 0.2rem;
      background: #fff;
      border-radius: 0.1rem;
      position: relative;
}
.chatroom .inBox .message p.item::before {
        content: "";
        display: block;
        width: 0;
        height: 0;
        position: absolute;
        left: -0.2rem;
        top: 0.28rem;
        transform: translateY(-50%);
        border-right: 0.1rem solid #fff;
        border-left: 0.1rem solid transparent;
        border-top: 0.1rem solid transparent;
        border-bottom: 0.1rem solid transparent;
}
.chatroom .outBox .message {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding: 0.2rem;
}
.chatroom .outBox .message .info {
      width: 0.56rem;
      height: 100%;
      margin: 0 0.2rem;
      min-height: 0.56rem;
      position: relative;
}
.chatroom .outBox .message .info img {
        display: block;
        border: none;
        width: 0.56rem;
        height: 0.56rem;
        border: 1px solid #2EB5E5;
        border-radius: 0.28rem;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
}
.chatroom .outBox .message p.item {
      font-size: 0.28rem;
      line-height: 0.36rem;
      min-height: 0.56rem;
      max-width: 70%;
      text-align: left;
      padding: 0.1rem 0.2rem;
      border-radius: 0.1rem;
      position: relative;
}
.chatroom .outBox .message p.item::before {
        content: "";
        display: block;
        width: 0;
        height: 0;
        position: absolute;
        left: -0.2rem;
        top: 0.28rem;
        transform: translateY(-50%);
        border-right: 0.1rem solid #2EB5E5;
        border-left: 0.1rem solid transparent;
        border-top: 0.1rem solid transparent;
        border-bottom: 0.1rem solid transparent;
}
.chatroom .outBox .msg-mine {
    display: block;
}
.chatroom .outBox .msg-mine .info {
      float: right;
}
.chatroom .outBox .msg-mine p.item {
      float: right;
      background: #2EB5E5;
}
.chatroom .outBox .msg-mine p.item::before {
        display: none;
}
.chatroom .outBox .msg-mine p.item::after {
        content: "";
        display: block;
        width: 0;
        height: 0;
        position: absolute;
        right: -0.2rem;
        top: 0.28rem;
        border: none;
        transform: translateY(-50%);
        border-left: 0.1rem solid #2EB5E5;
        border-right: 0.1rem solid transparent;
        border-top: 0.1rem solid transparent;
        border-bottom: 0.1rem solid transparent;
}
.chatroom p.item {
    word-break: break-all;
}
.chatroom .participant {
    width: 100%;
    text-align: center;
    position: relative;
}
.chatroom .participant span {
      display: inline-block;
      vertical-align: middle;
      margin: 0 auto;
      width: auto;
      padding: 0 0.2rem;
      max-width: 50%;
      background: #e5e5e5;
      color: #fff;
      border-radius: 0.25rem;
      font-size: 0.3rem;
      line-height: 0.5rem;
}
.input-box {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.78rem;
  line-height: 0.78rem;
  padding: 0.05rem;
  border-top: 1px solid #000;
  display: -ms-flexbox;
  display: flex;
}
.input-box input {
    border: none;
    width: 80%;
    height: 0.68rem;
    line-height: 0.68rem;
    vertical-align: middle;
    outline: none;
    font-size: 0.32rem;
    padding: 0.05rem;
    border-bottom: 1px solid #2EB5E5;
    background: #f5f5f5;
}
.input-box button {
    border: none;
    width: 18%;
    height: 0.68rem;
    margin-left: 0.1rem;
    background: #2EB5E5;
    text-align: center;
    font-size: 0.32rem;
    color: #fff;
    vertical-align: middle;
}



