
.wrapper-bg {
  width: 100%;
  height: 100%;
  background: #000;
  position: relative;
}
.wrapper-bg input[name="nickname"] {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 60%;
    height: 0.8rem;
    line-height: 0.8rem;
    border: none;
    background: #000;
    border-bottom: 1px solid #fff;
    outline: none;
    font-size: 0.6rem;
    text-align: center;
    color: #fff;
    font-weight: bold;
}



